html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit;
  margin: 0;
  padding: 0; }

body {
  background-color: #3767ad;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  min-height: 100vh;
  padding: 30px 0; }

.button {
  transition: transform 0.2s; }
  .button:hover {
    transform: scale(1.03); }
  .button__light {
    background-color: #fff;
    border: none;
    border-radius: 4px;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    font-weight: 500;
    height: 47px;
    width: 216px; }
  .button__show-history {
    margin-bottom: 30px; }

.button__add-glass {
  font-size: 18px;
  margin-bottom: 30px; }

.button__subtract-glass {
  background-color: #3767ad;
  border: none;
  color: #f5e5e7;
  font-weight: 500;
  margin-bottom: 30px; }

.glass {
  margin-bottom: 30px;
  position: relative; }
  .glass__basic-blue {
    bottom: 20px;
    position: absolute;
    right: 25px;
    transition: height 0.5s; }
  .glass__counter {
    bottom: 50px;
    font-size: 100px;
    font-weight: 500;
    margin: 0;
    position: absolute;
    text-align: center;
    width: 100%; }
    .glass__counter--red {
      color: #c52c2c; }

.history {
  color: #f5e5e7; }
  .history__heading {
    margin-bottom: 30px;
    text-align: center; }
  .history__list {
    list-style-type: none;
    margin-bottom: 0;
    opacity: 1;
    padding: 0 20px;
    transition: opacity 0.5s; }
    .history__list--hidden {
      opacity: 0;
      visibility: hidden; }

.main {
  align-items: center;
  display: flex;
  flex-direction: column;
  min-height: 90vh;
  justify-content: center;
  margin: 0 auto;
  width: 220px; }
  .main__header {
    color: #f5e5e7;
    font-size: 55px;
    font-weight: 700;
    margin: 0 0 30px 0; }
